"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { useAuthStore } from "@/lib/store"
import { useToast } from "@/hooks/use-toast"
import { ShoppingBag, Chrome } from "lucide-react"

interface LoginModalProps {
  isOpen: boolean
  onClose: () => void
  onLoginSuccess?: () => void
}

export function LoginModal({ isOpen, onClose, onLoginSuccess }: LoginModalProps) {
  const router = useRouter()
  const { login } = useAuthStore()
  const { toast } = useToast()
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [isLoading, setIsLoading] = useState(false)

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    const success = await login(email, password)

    if (success) {
      toast({
        title: "Login realizado",
        description: "Bem-vindo de volta!",
      })
      onClose()
      if (onLoginSuccess) {
        onLoginSuccess()
      }
    } else {
      toast({
        title: "Erro ao fazer login",
        description: "Email ou senha incorretos",
        variant: "destructive",
      })
    }

    setIsLoading(false)
  }

  const handleGoogleLogin = () => {
    // Mock Google login - in a real app, this would integrate with Google OAuth
    toast({
      title: "Login com Google",
      description: "Funcionalidade de login com Google seria implementada aqui",
    })
    // For demo purposes, we'll simulate a successful login
    setTimeout(() => {
      // Simulate setting user as authenticated
      // In a real app, this would come from the OAuth response
      onClose()
      if (onLoginSuccess) {
        onLoginSuccess()
      }
    }, 1000)
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-md">
        <DialogHeader className="text-center">
          <div className="mx-auto mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-primary">
            <ShoppingBag className="h-8 w-8 text-primary-foreground" />
          </div>
          <DialogTitle className="text-2xl">Bem-vindo</DialogTitle>
          <DialogDescription>Entre com sua conta para continuar</DialogDescription>
        </DialogHeader>
        <div className="space-y-4">
          <Button onClick={handleGoogleLogin} variant="outline" className="w-full">
            <Chrome className="mr-2 h-4 w-4" />
            Continuar com Google
          </Button>
          
          <div className="relative">
            <div className="absolute inset-0 flex items-center">
              <span className="w-full border-t" />
            </div>
            <div className="relative flex justify-center text-xs uppercase">
              <span className="bg-background px-2 text-muted-foreground">Ou continue com email</span>
            </div>
          </div>
          
          <form onSubmit={handleLogin} className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="modal-email">Email</Label>
              <Input
                id="modal-email"
                type="email"
                placeholder="seu@email.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="modal-password">Senha</Label>
                <Input
                  id="modal-password"
                  type="password"
                  placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </div>
              <Button type="submit" className="w-full" disabled={isLoading}>
                {isLoading ? "Entrando..." : "Entrar"}
              </Button>
            </form>
            
            <div className="text-center text-sm text-muted-foreground">
              <p>Demo: cliente@email.com / senha123</p>
              <p>Admin: admin@email.com / admin</p>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    )
}