"use client"

import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { ArrowLeft, MapPin, ShoppingCart } from "lucide-react"
import { useCartStore, useAuthStore } from "@/lib/store"
import { mockMarket } from "@/lib/mock-data"

interface ClientHeaderProps {
  showBack?: boolean
}

export function ClientHeader({ showBack = false }: ClientHeaderProps) {
  const router = useRouter()
  const itemCount = useCartStore((state) => state.getItemCount())
  const user = useAuthStore((state) => state.user)

  return (
    <header className="sticky top-0 z-20 border-b bg-background">
      <div className="flex items-center justify-between px-4 py-3">
        <div className="flex items-center gap-3">
          {showBack && (
            <Button variant="ghost" size="icon" onClick={() => router.back()}>
              <ArrowLeft className="h-5 w-5" />
            </Button>
          )}
          <div className="flex flex-col">
            <p className="text-xs text-muted-foreground">Ol√°, {user?.name?.split(" ")[0]}</p>
            <div className="flex items-center gap-1">
              <MapPin className="h-3 w-3 text-primary" />
              <span className="text-sm font-medium">{mockMarket.address}</span>
            </div>
          </div>
        </div>
        <Button variant="ghost" size="icon" className="relative" onClick={() => router.push("/client/cart")}>
          <ShoppingCart className="h-5 w-5" />
          {itemCount > 0 && (
            <Badge className="absolute -right-1 -top-1 h-5 w-5 rounded-full p-0 text-xs">{itemCount}</Badge>
          )}
        </Button>
      </div>
    </header>
  )
}