"use client"

import Image from "next/image"
import { useState } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Plus, Minus } from "lucide-react"
import { formatCurrency } from "@/lib/currency"
import { useCartStore, useAuthStore } from "@/lib/store"
import { LoginModal } from "@/components/client/login-modal"
import type { Product } from "@/lib/types"

interface ProductCardProps {
  product: Product
}

export function ProductCard({ product }: ProductCardProps) {
  const [isLoginModalOpen, setIsLoginModalOpen] = useState(false)
  const { items, addItem, updateQuantity } = useCartStore()
  const { isAuthenticated } = useAuthStore()
  const cartItem = items.find((item) => item.productId === product.id)
  const quantity = cartItem?.quantity || 0

  const handleAdd = () => {
    if (!isAuthenticated) {
      setIsLoginModalOpen(true)
      return
    }
    addItem(product.id, 1)
  }

  const handleIncrease = () => {
    updateQuantity(product.id, quantity + 1)
  }

  const handleDecrease = () => {
    updateQuantity(product.id, quantity - 1)
  }

  const handleLoginSuccess = () => {
    // Add the product to cart after successful login
    addItem(product.id, 1)
  }

  const hasDiscount = product.originalPrice && product.originalPrice > product.price

  return (
    <>
      <Card className="overflow-hidden">
        <CardContent className="p-0">
          <div className="relative aspect-square bg-muted">
            <Image src={product.image || "/placeholder.svg"} alt={product.name} fill className="object-cover" />
            {hasDiscount && (
              <Badge className="absolute right-2 top-2 bg-destructive text-destructive-foreground">
                {Math.round(((product.originalPrice! - product.price) / product.originalPrice!) * 100)}% OFF
              </Badge>
            )}
          </div>
          <div className="space-y-2 p-3">
            <h3 className="line-clamp-2 text-sm font-medium leading-tight">{product.name}</h3>
            <div className="space-y-1">
              {hasDiscount && (
                <p className="text-xs text-muted-foreground line-through">{formatCurrency(product.originalPrice!)}</p>
              )}
              <p className="text-lg font-bold text-primary">{formatCurrency(product.price)}</p>
              <p className="text-xs text-muted-foreground">por {product.unit}</p>
            </div>
            {quantity === 0 ? (
              <Button onClick={handleAdd} className="w-full" size="sm">
                <Plus className="mr-1 h-4 w-4" />
                Adicionar
              </Button>
            ) : (
              <div className="flex items-center justify-between rounded-lg border bg-background">
                <Button variant="ghost" size="icon" onClick={handleDecrease} className="h-8 w-8">
                  <Minus className="h-4 w-4" />
                </Button>
                <span className="font-semibold">{quantity}</span>
                <Button variant="ghost" size="icon" onClick={handleIncrease} className="h-8 w-8">
                  <Plus className="h-4 w-4" />
                </Button>
              </div>
            )}
          </div>
        </CardContent>
      </Card>

      <LoginModal 
        isOpen={isLoginModalOpen} 
        onClose={() => setIsLoginModalOpen(false)}
        onLoginSuccess={handleLoginSuccess}
      />
    </>
  )
}